import { useState } from "react";
import { Button } from "@heroui/button";
import { Input } from "@heroui/input";
import { Card, CardHeader, CardBody, CardFooter } from "@heroui/card";
import { Listbox, ListboxItem } from "@heroui/listbox";
import { FiMenu, FiPlus, FiSearch, FiFolder, FiFile } from "react-icons/fi";

type Note = {
  id: string;
  title: string;
  content: string;
  folder: string;
  createdAt: Date;
};

export default function StickyNotes() {
  const [isDrawerOpen, setIsDrawerOpen] = useState(true);
  const [searchQuery, setSearchQuery] = useState("");
  const [notes, setNotes] = useState<Note[]>([
    {
      id: "1",
      title: "Welcome Note",
      content: "This is your first sticky note!",
      folder: "General",
      createdAt: new Date(),
    },
  ]);
  const [folders, setFolders] = useState(["General", "Work", "Personal"]);

  const filteredNotes = notes.filter(note =>
    note.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
    note.content.toLowerCase().includes(searchQuery.toLowerCase())
  );

  return (
    <div className="flex h-screen bg-default-50">
      {/* Drawer */}
      <div className={`${isDrawerOpen ? "w-64" : "w-0"} transition-all duration-300 overflow-hidden`}>
        <div className="p-4 h-full border-r border-divider">
          <Listbox aria-label="Folders">
            {folders.map(folder => (
              <ListboxItem key={folder} startContent={<FiFolder />}>
                {folder}
              </ListboxItem>
            ))}
          </Listbox>
          <Button className="mt-4" startContent={<FiPlus />}>
            New Folder
          </Button>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 flex flex-col overflow-hidden">
        {/* Header */}
        <header className="p-4 border-b border-divider flex items-center gap-4">
          <Button isIconOnly onClick={() => setIsDrawerOpen(!isDrawerOpen)}>
            <FiMenu />
          </Button>
          <Input
            placeholder="Search notes..."
            startContent={<FiSearch />}
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="max-w-md"
          />
        </header>

        {/* Notes Grid */}
        <main className="flex-1 p-6 overflow-auto">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {filteredNotes.map(note => (
              <Card key={note.id} className="h-48">
                <CardHeader className="font-semibold">{note.title}</CardHeader>
                <CardBody className="text-sm text-default-500">
                  {note.content}
                </CardBody>
                <CardFooter className="text-xs text-default-400">
                  {note.createdAt.toLocaleDateString()}
                </CardFooter>
              </Card>
            ))}
          </div>
        </main>

        {/* New Note FAB */}
        <Button
          isIconOnly
          color="primary"
          className="fixed bottom-6 right-6"
          onClick={() => {
            setNotes(prev => [...prev, {
              id: Date.now().toString(),
              title: "New Note",
              content: "",
              folder: "General",
              createdAt: new Date()
            }])
          }}
        >
          <FiPlus />
        </Button>
      </div>
    </div>
  );
}
